.footer {
  background-color: #041827;
  color: rgba(255, 255, 255, 0.7);
}

.footer__wrapper {
  margin: 0 auto;
  max-width: 1220px;
  padding: 50px 0 44px;

  @include vp-1023 {
    padding: 50px 26px 54px 12px;
  }

  @include vp-767 {
    padding: 35px 26px 54px 8px;
  }
}

.footer__partitions {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr 500px 1fr;

  @include vp-1023 {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 16px;
  }

  @include vp-1023 {
    display: flex;
    flex-direction: column;
  }
}

.footer__partittion {
  @include vp-1023 {
    grid-column: 1 / -1;
  }

  @include vp-767 {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      right: -42px;
      width: 1000%;
      height: 1px;
      background-color: rgba(248, 253, 255, 0.7);
    }
  }
}

.footer__partittion-button {
  position: absolute;
  right: 0;
  bottom: 0;
  // background-color: inherit;
  border: none;

  @media (min-width: vp-1023) {
    display: none;
  }

  @include vp-767 {
    width: 15px;
    height: 15px;
  }
}

.footer__partittion--hidden ul {
  @include vp-767 {
    display: none;
  }
}
